@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;700&family=Playfair+Display:wght@700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  /* Standards */
  background-color: #262b46;
  color: #fff;
  font-family: 'Montserrat', sans-serif;
  
}
h1,
h2,
h3 {
  font-family: 'Playfair Display', serif;
}

a {
  color: #fff;
  text-decoration: none;
}

* {
  scroll-behavior: smooth;
}


.detail_title {
  text-align: center;
  font-size: 2em;
}

.details {
  display: flex;
  justify-content: center;
  margin: 4vh auto 0 !important;
  max-width: 90vw;
  line-height: 1.5rem;
}

.detail_ingredient_item {
  display: block;
}

.right_article {
  max-width: 450px;
  padding-left: 20px;
}

@media only screen and (max-width: 800px) {
  .details {
    flex-wrap: wrap;
  }

  .right_article {
    max-width: 450px;
    padding-left: 0;
  }
}

.start {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  color: #fff;
  padding: 0;
}
.start div {
  padding: 5vh 0 5vh 5%;
  font-family: Playfair Display;
  font-size: 1.2em;
}
.Gin {
  background-color: #6fc3df;
}
.Vodka {
  background-color: #8d82c4;
}
.Rum {
  background-color: #ec8d81;
}
.Scotch {
  background-color: #e7b788;
}
.A_frei {
  background-color: #8ea9e8;
}
.Random {
  background-color: #87c5a4;
}
.Gin, .Vodka, .Rum, .Scotch, .A_frei, .Random {
  transition: 0.1s;
}
.Gin:hover, .Vodka:hover, .Rum:hover, .Scotch:hover, .A_frei:hover, .Random:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

@media only screen and (max-width: 576px) {
  .start {
    display: grid;
    grid-template-columns: 1fr;
  }
}

.drink-container {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  text-align: center;
  padding: 5vh;
}
.drink-image {
  width: 100%;
}
.drink-name {
  font-family: Montserrat;
  font-weight: 700;
  -webkit-transform: rotate(-90deg);
          transform: rotate(-90deg);
}

.style1 {
  background-color: #6fc3df;
}
.style2 {
  background-color: #8d82c4;
}
.style3 {
  background-color: #ec8d81;
}
.style4 {
  background-color: #e7b788;
}
.style5 {
  background-color: #8ea9e8;
}
.style6 {
  background-color: #87c5a4;
}
.left,
.right {
  position: relative;
  transition: 0.1s;
}
.left:hover,
.right:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
  z-index: 99;
}
.left img {
  grid-column: 1 / 2;
}
.left h2 {
  grid-column: 2 / 3;
}
.right img {
  grid-column: 2 / 3;
}
.right h2 {
  grid-column: 1 / 2;
  position: absolute;
  top: 40%;
  right: 70%;
}

.HeaderContainer {
  padding: 0 5%;
}
.Navbar {
  text-transform: uppercase;
  list-style: none;
  display: flex;
  justify-content: space-between;
  -webkit-padding-start: 0;
          padding-inline-start: 0;
  padding: 2vh 0;
  margin: 0;
  font-size: 0.81rem;
}
.logo {
  font-weight: bold;
  letter-spacing: 3px;
  font-family: Playfair Display;
}
.menu {
  font-family: 'Montserrat', sans-serif;
}
.main_Text {
  font-weight: bold;
  font-size: 3rem;
  margin: 14vh 0 0 0;
}
.sub_Text {
  font-family: 'Montserrat', sans-serif;
  font-size: 0.875rem;
  margin: 1vh 0 3vh 0;
}
.text_Input {
  border-radius: 6px;
  height: 2.7rem;
  box-sizing: border-box;
  margin: 5vh 2% 3vh 0;
  font-size: 1rem;
  text-align: center;
}
.arrow-container {
  display: flex;
  flex-direction: row;
  justify-content: center;
}
.arrow {
  background-color: #262b46;
  border: none;
  padding-bottom: 50px;
}
.arrow:hover {
  cursor: pointer;
  transition: -webkit-transform 3s ease-in-out;
  transition: transform 3s ease-in-out;
  transition: transform 3s ease-in-out, -webkit-transform 3s ease-in-out;
  -webkit-transform: rotateY(-360deg);
          transform: rotateY(-360deg);
}
.search-button {
  border-radius: 6px;
  background-color: #6fc3df;
  color: #000;
  padding: 10px;
  vertical-align: middle;
}
.search-button:hover {
  background-color: goldenrod;
  color: #fff;
  transition: all 2s ease;
}




@media only screen and (max-width: 576px) {
  .HeaderContainer {
    height: 65vh;
  }
  .arrow {
    padding: 0 0 5vh 0;
  }
}

.footer {
  background-color: #262b46;
  padding-left: 5%;
  color: #fff;
  padding-bottom: 10vh;
}

.footer h2 {
  font-size: 1.75rem;
  margin-top: 0;
  padding: 10vh 0 3vh 0;
  font-family: Playfair Display;
}

.footer p {
  font-family: 'Montserrat', sans-serif;
  font-size: 1rem;
  line-height: 1.5rem;
  padding-right: 10%;
}

.category-results-container {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  /* width: 100%;
  height: 60%;
  padding-left: 0;
  overflow-y: scroll; */
}

@media only screen and (max-width: 576px) {
  .category-results-container {
    grid-template-columns: repeat(1, 1fr);
  }
}

